*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0; }

.page {
  box-sizing: border-box;
  color: #1b1c21;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  background-color: #7e7e82; }
  .page__container {
    max-width: 500px;
    margin: 80px 60px; }

.input {
  width: 100%;
  height: 60px;
  padding: 0 15px;
  line-height: 60px;
  font-size: 40px;
  border: 0; }

.option {
  position: relative;
  height: 40px;
  padding: 0 15px;
  line-height: 36px;
  font-size: 30px;
  background-color: #e3e3e3;
  border: 2px solid #1b1c21;
  cursor: pointer;
  transition: all 0.2s; }
  .option:hover {
    background-color: #65cdf9; }

.repo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #1b1c21;
  background-color: #e27beb;
  height: 100px;
  padding: 10px 15px;
  font-size: 20px; }
  .repo__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
  .repo__btn-close {
    margin-right: 12px; }

.search {
  position: relative;
  margin-bottom: 45px; }
  .search:before {
    visibility: hidden;
    content: "";
    position: absolute;
    height: 40px;
    width: 40px;
    right: 10px;
    top: 10px;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url(./static/loader.svg); }
  .search--loader:before {
    visibility: visible; }

.btn {
  width: 44px;
  height: 44px;
  flex-shrink: 0;
  background-color: #e27beb;
  background-repeat: no-repeat;
  background-position: center;
  border: 0;
  cursor: pointer;
  transition: all 0.4s; }
  .btn:hover {
    transform: scale(1.2); }
  .btn--theme_delete {
    background-image: url(./static/delete.svg); }

